.social ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  justify-content: space-between;
  min-width: 106px;

  @media (max-width: $vp-1024) {
    min-width: 87px;
  }
}

.social ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;

  @media (max-width: $vp-1024) {
    width: 16px;
    height: 16px;
  }

  @media (max-width: $vp-768) {
  }
}
.social li:last-child {
}
