.card__back a {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  width: 210px;
  min-height: 40px;
  font-size: 14px;
  font-weight: 600;
  line-height: 21px;
  background-color: #f9fbfd; }

.booking {
  padding: 50px 0;
  min-height: 555px;
  background-image: url("../../img/content/booking-bg-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

.booking__wrapper {
  display: grid;
  grid-template-columns: 500px 500px; }

.booking-wrapper__form {
  display: flex;
  flex-direction: column; }

.form {
  display: flex;
  flex-direction: column;
  padding: 50px;
  width: 403px;
  background-color: #011c40; }

.form p {
  margin: 0;
  margin-bottom: 35px;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd; }

.form label {
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd; }

.form input {
  margin-bottom: 20px;
  padding: 0 16px;
  width: 100%;
  height: 43px;
  border: none;
  font-size: 14px;
  line-height: 21px; }

.form input[type="checkbox"] {
  display: none; }

.form input[type="checkbox"] + label {
  cursor: pointer; }
  .form input[type="checkbox"] + label::before {
    content: "";
    display: inline-block;
    margin-right: 5px;
    width: 18px;
    height: 18px;
    vertical-align: middle;
    background-color: #f9fbfd; }

.form input[type="checkbox"]:checked + label::before {
  background-image: url("../../img/svg/checkbox.svg");
  background-repeat: no-repeat;
  background-position: center; }

.form button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 0;
  min-height: 43px;
  font-weight: 600;
  line-height: 21px;
  border: none; }

.contacts {
  padding: 120px 0; }

.contacts__wrapper-map-static {
  display: none; }

.contacts__wrapper {
  display: grid;
  grid-template-columns: 560px 500px;
  column-gap: 100px; }

.contacts__wrapper-text {
  padding: 50px 0; }

.contacts__wrapper-text p {
  margin: 0;
  margin-bottom: 45px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px; }

.contacts__wrapper-text address {
  position: relative;
  padding-left: 32px; }
  .contacts__wrapper-text address::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 18px;
    height: 22px;
    background-image: url("../img/svg/sprite/map-marker.svg"); }

.contacts__wrapper-text a {
  position: relative;
  padding-left: 28px; }
  .contacts__wrapper-text a::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 18px;
    height: 22px;
    background-image: url("../img/svg/sprite/phone.svg"); }

.contacts__wrapper-text address {
  margin-bottom: 30px;
  font-style: normal; }

.contacts__wrapper-text a {
  display: block;
  margin-bottom: 30px; }

.page-footer {
  padding: 50px 0;
  background-color: #011c40; }

.page-footer__wrapper {
  display: flex;
  align-items: center; }
  @media (max-width: 768px) {
    .page-footer__wrapper {
      flex-direction: column;
      align-items: flex-start; } }

.page-footer__nav {
  margin-right: auto; }
  @media (max-width: 768px) {
    .page-footer__nav {
      flex-direction: column;
      align-items: flex-start; } }

.page-footer__logo {
  margin-right: 40px;
  width: 70px;
  height: 38px; }
  @media (max-width: 1024px) {
    .page-footer__logo {
      width: 38px;
      height: 20px; } }
  @media (max-width: 768px) {
    .page-footer__logo {
      margin-bottom: 30px;
      width: 42px;
      height: 22px; } }

.social ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  justify-content: space-between;
  min-width: 106px; }
  @media (max-width: 1024px) {
    .social ul {
      min-width: 87px; } }

.social ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px; }
  @media (max-width: 1024px) {
    .social ul li {
      width: 16px;
      height: 16px; } }

